"use client";

import React, { useState } from "react";
import Button from "@/components/UI/Button";

const ResetForm: React.FC = () => {
  const [email, setEmail] = useState("");
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);

    try {
      if (!email || !email.includes("@")) {
        throw new Error("Por favor, ingresa un correo electrónico válido.");
      }

      // Simula el envío del correo de recuperación
      alert("Correo de recuperación enviado.");
    } catch (err: any) {
      setError(err.message);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="flex flex-col gap-4">
      <h1 className="text-xl font-bold text-gray-800">Recuperar contraseña</h1>
      <p className="text-sm text-gray-600">
        Si olvidaste tu contraseña, no te preocupes. Te enviaremos un correo con las instrucciones para recuperarla.
      </p>

      <div className="relative">
        <input
          type="email"
          placeholder="Correo electrónico"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:outline-none"
        />
      </div>

      {error && <p className="text-sm text-red-500">{error}</p>}

      <Button type="submit">Recuperar contraseña</Button>
    </form>
  );
};

export default ResetForm;
